import Vue from 'vue'
import Router from 'vue-router'
import BlogList from '@/views/BlogList'
import WebNav from '@/views/WebNav'
import AboutMe from '@/views/AboutMe'
import HelloWorld from '@/components/HelloWorld'
import mycom from '@/components/mycom'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'BlogList',
      component: BlogList
    }, 
    {
      path: '/WebNav',
      name: 'WebNav',
      component: WebNav
    },
    {
      path: '/AboutMe',
      name: 'AboutMe',
      component: AboutMe
    }, 
    {
      path: '/a',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/mycom',
      name: 'mycom',
      component: mycom
    }
  ]
})



